









































































var context = null;
export default {
	data() {
		return {
			cardInfo: {
				realname: '',
				is_validate: '',
				card: '',
				card_img_a: '../../../static/card_a.png',
				card_img_b: '../../../static/card_b.png',
			},
			loading: false,
			card_a: [],
			card_b: [],
			card_a_data: '',
			card_b_data: '',
			progress:0,
			cwidth:'1000px',
			cheight:'1000px',
		}
	},
	onReady: function () {
		context = uni.createCanvasContext('canvas')
	},
	onLoad() {
		this.getIdCard();
	},
	onShow() {
		
	},
	methods: {
		getIdCard: function() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			try {
				this.$http.post(this.websiteUrl + '/api/api/getIdCard', {
					openid,
					sessionKey,
				}, (res) => {
					if (res.data.code == 1) {
						this.cardInfo = res.data.result.cardInfo;
						if(this.cardInfo.card == null){
							this.cardInfo.card = '';
						}
						
						if(this.cardInfo.realname == null){
							this.cardInfo.realname = '';
						}
					}else{
						uni.showModal({
							content: res.data.msg,
							showCancel: false
						});
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				});
			}
		},
		addIdCard: function() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const realname = this.cardInfo.realname;
			const card = this.cardInfo.card;
			//const card_a_data = this.card_a_data;
			//const card_b_data = this.card_b_data;
			if (realname == '') {
				uni.showModal({
					content: '请填写真实姓名',
					showCancel: false
				});
				return;
			}
			if (card == '') {
				uni.showModal({
					content: '请填写身份证号码',
					showCancel: false
				});
				return;
			}
			/*if (card_a_data == '') {
				uni.showModal({
					content: '请上传身份证正面',
					showCancel: false
				});
				return;
			}
			if (card_b_data == '') {
				uni.showModal({
					content: '请上传身份证反面',
					showCancel: false
				});
				return;
			}*/
			const that = this;
			try {
				if (this.loading) {
					return;
				}
				this.loading = true;

				this.$http.post(this.websiteUrl + '/api/api/addIdCard', {
					openid,
					sessionKey,
					realname,
					card,
					//card_a_data,
					//card_b_data,
				}, (res) => {
					uni.showModal({
						content: res.data.msg,
						showCancel: false,
						success: function() {
							that.loading = false;
							//uni.navigateBack();
							uni.redirectTo({
								url: '/pages/user/info/realname'
							});
						}
					})

				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false,
					success: function() {
						that.loading = false;
					}
				});
			}
		},
		upFile:function(img,side) {
				const that = this;
				const openid = this.$store.state.openid;
				const sessionKey = this.$store.state.sessionKey;
				
				const uploadTask = uni.uploadFile({
					url: this.websiteUrl + '/api/upload/uploadcard', //仅为示例，非真实的接口地址
					filePath: img,
					name: 'file',
					formData: {
						'data': JSON.stringify({
							openid,
							sessionKey,
							side
						})
					},
					success: (uploadFileRes) => {
						const data = JSON.parse(uploadFileRes.data);
						uni.hideLoading();
						if (data.code == 1) {
							if(side == 'front'){
								if(data.result.juhe == 1){
									//三方识别
									if(data.result.juhedata.error_code == 0){
										that.card_a_data = data.result.img_url;
										//that.cardInfo.card_img_a = data.result.card_img;
										that.cardInfo.realname = data.result.juhedata.result.realname;
										that.cardInfo.card = data.result.juhedata.result.idcard;
										uni.showToast({
											title: "成功"
										})
									}else{
										uni.showModal({
											content: data.result.juhedata.reason,
											showCancel: false
										});
									}
								}else{
									that.card_a_data = data.result.img_url;
									uni.showToast({
										title: "成功"
									})
								}
							}else{
								if(data.result.juhe == 1){
									//三方识别
									if(data.result.juhedata.error_code == 0){
										that.card_b_data  = data.result.img_url;
										//that.cardInfo.card_img_b = data.result.card_img;
										uni.showToast({
											title: "成功"
										})
									}else{
										uni.showModal({
											content: data.result.juhedata.reason,
											showCancel: false
										});
									}
								}else{
									that.card_b_data = data.result.img_url;
									uni.showToast({
										title: "成功"
									})
								}
							}
						} else {
							uni.showToast({
								title: data.msg
							})
						}
					},
					fail: (e) => {
						console.log(e);
						uni.hideLoading();
						uni.showToast({
							title: "上传失败",
						})
			
					},
					complete:(e) => {
						that.progress = 0;
					}
				});
				
				uploadTask.onProgressUpdate((res) => {
					that.progress = res.progress;
					//console.log('上传进度' + res.progress);
					//console.log('已经上传的数据长度' + res.totalBytesSent);
					//console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend);
			
					// 测试条件，取消上传任务。
					//if (res.progress > 50) {
						//uploadTask.abort();
					//}
				});
		},
		resethb: function () {
			//context.clearRect(0, 0, this.cwidth, this.cheight);
			//context.draw()
		},
		chooseImage: async function() {
			const that = this;
			that.progress = 0;
			uni.chooseImage({
				sizeType: ['compressed'],
				sourceType: ['camera'],
				count: 1,
				success: (res) => {
					this.card_a = [];
					this.card_a = this.card_a.concat(res.tempFilePaths);
					//console.log(res.tempFilePaths)
					const index = this.card_a.length;
					if (index >= 1) {
						this.cardInfo.card_img_a = this.card_a[index - 1];
						const tempFilePaths = res.tempFilePaths;
						const side = 'front';
						uni.showLoading({title:'识别中...'});
						try {
							uni.getImageInfo({
								src: tempFilePaths[index - 1],
								success:  (image)=> {
									//console.log(image.width);
									//console.log(image.height);
									that.resethb();
									if(image.width > 1000){
										that.cwidth = (image.width/7)+'px';
										that.cheight = (image.height/7)+'px';
										context.drawImage(tempFilePaths[index - 1], 0, 0,image.width/7,image.height/7);
										context.draw(false,function(){
											uni.canvasToTempFilePath({
												canvasId: 'canvas',
												success: function (g) {
													that.upFile(g.tempFilePath,side);
												},
												fail: function (err) {
													console.error(JSON.stringify(err))
													uni.hideLoading();
													uni.showToast({
														title: "图片压缩失败",
													})
												}
											})
										});
										
										//setTimeout(,3000);
									}else{
										that.upFile(tempFilePaths[index - 1],side);
									}
									
								}
							});
						
							//console.log(res);
							return;
							
						} catch (e) {
							uni.hideLoading();
						}
					} else {
						uni.showToast({
							title: "没有选中图片"
						})
					}
				}
			})
		},
		chooseImageB: async function() {
			const that = this;
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			that.progress = 0;
			uni.chooseImage({
				sizeType: ['compressed'],
				sourceType: ['camera'],
				count: 1,
				success: (res) => {
					this.card_b = [];
					this.card_b = this.card_b.concat(res.tempFilePaths);
					//console.log(res.tempFilePaths)
					const index = this.card_b.length;
					if (index >= 1) {
						this.cardInfo.card_img_b = this.card_b[index - 1];
						const tempFilePaths = res.tempFilePaths;
						const side = 'back';
						uni.showLoading({title:'识别中...'});
						try {
							uni.getImageInfo({
								src: tempFilePaths[index - 1],
								success:  (image)=> {
									//console.log(image.width);
									//console.log(image.height);
									that.resethb();
									if(image.width > 1000){
										that.cwidth = (image.width/7)+'px';
										that.cheight = (image.height/7)+'px';
										context.drawImage(tempFilePaths[index - 1], 0, 0,image.width/7,image.height/7);
										context.draw(false,function(){
											uni.canvasToTempFilePath({
												canvasId: 'canvas',
												success: function (g) {
													that.upFile(g.tempFilePath,side);
												},
												fail: function (err) {
													console.error(JSON.stringify(err))
													uni.hideLoading();
													uni.showToast({
														title: "图片压缩失败",
													})
												}
											})
										});
										
									}else{
										that.upFile(tempFilePaths[index - 1],side);
									}
									
								}
							});
														
							//console.log(res);
							return;
						} catch (e) {
							uni.hideLoading();
						}
					} else {
						uni.showToast({
							title: "没有选中图片"
						})
					}
				}
			})
		},
	}
}
